import React, { PureComponent } from 'react';
import TextAnchor from './TextAnchor';

const edgeLength = 15;
const textOffset = 16;
const quarterCircle = Math.PI / 2;

class LineMeasurement extends PureComponent {

  constructor(props) {
    super(props);
    this.state = { midHover: false };
  }

  componentDidMount() {
    this.startGrabber.addEventListener('mousedown', this.onStartDown);
    this.midGrabber.addEventListener('mousedown', this.onMidDown);
    this.midGrabber.addEventListener('mouseenter', this.onMidEnter);
    this.midGrabber.addEventListener('mouseleave', this.onMidLeave);
    this.endGrabber.addEventListener('mousedown', this.onEndDown);
    document.addEventListener('mousemove', this.onMouseMove);
    window.addEventListener('mouseup', this.onMouseUp);
    window.addEventListener('blur', this.endDrag);
  }

  componentWillUnmount() {
    this.startGrabber.removeEventListener('mousedown', this.onStartDown);
    this.midGrabber.removeEventListener('mousedown', this.onMidDown);
    this.midGrabber.removeEventListener('mouseenter', this.onMidEnter);
    this.midGrabber.removeEventListener('mouseleave', this.onMidLeave);
    this.endGrabber.removeEventListener('mousedown', this.onEndDown);
    document.removeEventListener('mousemove', this.onMouseMove);
    window.removeEventListener('mouseup', this.onMouseUp);
    window.removeEventListener('blur', this.endDrag);
  }

  render() {
    // Line layout:
    const startX = this.props.line.startX * this.props.parentWidth;
    const startY = this.props.line.startY * this.props.parentHeight;
    const endX = this.props.line.endX * this.props.parentWidth;
    const endY = this.props.line.endY * this.props.parentHeight;
    const deltaX = endX - startX;
    const deltaY = endY - startY;
    const rotate = Math.atan2(deltaY, deltaX);
    const edgeX = edgeLength * Math.sin(rotate) / 2.0;
    const edgeY = edgeLength * Math.cos(rotate) / 2.0;

    // Text layout (make sure the text is never rotated so much to be upside down):
    const centerX = (startX + endX) / 2;
    const centerY = (startY + endY) / 2;
    const rotateIsSmall = Math.abs(rotate) <= quarterCircle;
    const offsetX = (rotateIsSmall ? -1 : 1) * textOffset * Math.sin(rotate);
    const offsetY = (rotateIsSmall ? 1 : -1) * textOffset * Math.cos(rotate);
    const textX = centerX + offsetX;
    const textY = centerY + offsetY;
    const textRotate = Math.atan2(offsetY, offsetX) - quarterCircle;

    const text = this.props.formatDistance(this.props.measureLine(this.props.line));
    const rootClassName = 'line-measurement' + (this.state.midHover ? ' mid-hover' : '');

    return (
      <div className={rootClassName} ref={e => this.root = e}>
        <TextAnchor x={textX} y={textY} rotate={textRotate} onDeleteButtonClick={this.onDeleteButtonClick}>
          <div className='measurement-text' ref={e => this.text = e}>{text}</div>
        </TextAnchor>
        <svg className='measurement-svg'>
          <g className='grabber-group'>
            <line className='grabber mid-grabber' x1={startX} y1={startY} x2={endX} y2={endY} ref={e => this.midGrabber = e} />
            <line className='line mid-line' x1={startX} y1={startY} x2={endX} y2={endY} ref={e => this.midLine = e} />
          </g>
          <g className='grabber-group'>
            <line className='grabber start-grabber' x1={startX - edgeX} y1={startY + edgeY} x2={startX + edgeX} y2={startY - edgeY} ref={e => this.startGrabber = e} />
            <line className='line start-line' x1={startX - edgeX} y1={startY + edgeY} x2={startX + edgeX} y2={startY - edgeY} ref={e => this.startLine = e} />
          </g>
          <g className='grabber-group'>
            <line className='grabber end-grabber' x1={endX - edgeX} y1={endY + edgeY} x2={endX + edgeX} y2={endY - edgeY} ref={e => this.endGrabber = e} />
            <line className='line end-line' x1={endX - edgeX} y1={endY + edgeY} x2={endX + edgeX} y2={endY - edgeY} ref={e => this.endLine = e} />
          </g>
        </svg>
      </div>
    );
  }

  onStartDown = event => {
    if (event.button === 0) {
      this.startDragInProgress = true;
      this.onDragBegin(event);
    }
  }

  onMidDown = event => {
    if (event.button === 0) {
      this.midDragInProgress = true;
      this.onDragBegin(event);
    }
  }

  onEndDown = event => {
    if (event.button === 0) {
      this.endDragInProgress = true;
      this.onDragBegin(event);
    }
  }

  onDragBegin = event => {
    event.preventDefault();
    this.mouseXAtPress = event.clientX;
    this.mouseYAtPress = event.clientY;
    this.lineAtPress = this.props.line;
    this.startXAtPress = this.props.line.startX * this.props.parentWidth;
    this.startYAtPress = this.props.line.startY * this.props.parentHeight;
    this.endXAtPress = this.props.line.endX * this.props.parentWidth;
    this.endYAtPress = this.props.line.endY * this.props.parentHeight;
  }

  onMouseMove = event => {
    if ((this.startDragInProgress || this.endDragInProgress || this.midDragInProgress) && !this.dragOccurred) {
      this.dragOccurred = true;
      this.toggleDragStyles();
    }

    const clientX = event.clientX;
    const clientY = event.clientY;
    if (this.startDragInProgress) {
      const startX = this.clamp(this.getXAfterDrag(this.startXAtPress, clientX));
      const startY = this.clamp(this.getYAfterDrag(this.startYAtPress, clientY));
      this.props.onChange({ ...this.props.line, startX, startY });
    } else if (this.endDragInProgress) {
      const endX = this.clamp(this.getXAfterDrag(this.endXAtPress, clientX));
      const endY = this.clamp(this.getYAfterDrag(this.endYAtPress, clientY));
      this.props.onChange({ ...this.props.line, endX, endY });
    } else if (this.midDragInProgress) {
      let startX = this.getXAfterDrag(this.startXAtPress, clientX);
      let startY = this.getYAfterDrag(this.startYAtPress, clientY);
      let endX = this.getXAfterDrag(this.endXAtPress, clientX);
      let endY = this.getYAfterDrag(this.endYAtPress, clientY);
      const deltaX = endX - startX;
      const deltaY = endY - startY;
      // Don't let the line be dragged outside the layer bounds:
      if (startX < 0) {
        startX = 0;
        endX = deltaX;
      } else if (startX > 1) {
        startX = 1;
        endX = 1 + deltaX;
      }
      if (startY < 0) {
        startY = 0;
        endY = deltaY;
      } else if (startY > 1) {
        startY = 1;
        endY = 1 + deltaY;
      }
      if (endX < 0) {
        startX = -deltaX;
        endX = 0;
      } else if (endX > 1) {
        startX = 1 - deltaX;
        endX = 1;
      }
      if (endY < 0) {
        startY = -deltaY;
        endY = 0;
      } else if (endY > 1) {
        startY = 1 - deltaY;
        endY = 1;
      }
      this.props.onChange({ ...this.props.line, startX, startY, endX, endY });
    }
  }

  getXAfterDrag = (xAtPress, clientX) => (xAtPress + clientX - this.mouseXAtPress) / this.props.parentWidth;

  getYAfterDrag = (yAtPress, clientY) => (yAtPress + clientY - this.mouseYAtPress) / this.props.parentHeight;

  onMouseUp = event => this.endDrag();

  endDrag = () => {
    if (this.dragOccurred) {
      this.toggleDragStyles();
      this.dragOccurred = false;
    }
    const anyDragAttempted = this.startDragInProgress || this.midDragInProgress || this.endDragInProgress;
    if (this.startDragInProgress) {
      this.startDragInProgress = false;
    }
    if (this.midDragInProgress) {
      this.midDragInProgress = false;
    }
    if (this.endDragInProgress) {
      this.endDragInProgress = false;
    }
    if (anyDragAttempted && this.didValuesChange()) {
      this.props.onRelease(this.props.line);
    }
  }

  didValuesChange = () => this.props.line.startX !== this.lineAtPress.startX
    || this.props.line.startY !== this.lineAtPress.startY
    || this.props.line.endX !== this.lineAtPress.endX
    || this.props.line.endY !== this.lineAtPress.endY

  onMidEnter = event => this.setState({ ...this.state, midHover: true });

  onMidLeave = event => this.setState({ ...this.state, midHover: false });

  getAnnotationLayerClassList = () => this.root.parentElement.classList;

  clamp = value => Math.min(1, Math.max(0, value));

  toggleDragStyles = () => {
    if (this.startDragInProgress) {
      this.startLine.classList.toggle('dragged');
      this.getAnnotationLayerClassList().toggle('line-start-dragged');
    }
    if (this.midDragInProgress) {
      this.startLine.classList.toggle('dragged');
      this.midLine.classList.toggle('dragged');
      this.endLine.classList.toggle('dragged');
      this.getAnnotationLayerClassList().toggle('line-mid-dragged');
    }
    if (this.endDragInProgress) {
      this.endLine.classList.toggle('dragged');
      this.getAnnotationLayerClassList().toggle('line-end-dragged');
    }
    this.getAnnotationLayerClassList().toggle('any-dragged');
  }

  onDeleteButtonClick = () => this.props.onDeleteButtonClick(this.props.line);
}

export default LineMeasurement;


